.createTodoButton {
    background-color: rgba(66, 219, 168, 0.945);
    height: 50px;
    width: 50px;
    border: 2px solid rgba(11, 167, 115, 0.945);
    border-radius: 50%;
    text-align: center;
    font-size: 30px;
    padding-bottom: 2px;
    padding-left: 5px;
    color: rgba(0, 136, 91, 0.945);
    position: fixed;
    right: 20px;
    bottom: 10px;
    z-index: 1;
}